<template>
  <div class="lab">
    <div class="w3-top pc-hidden header">
      <appHeader></appHeader>
    </div>
    <div class="w3-display-position lab-ctn">
      <div class="lab-ctn-bg"></div>
      <div class="lab-ctn-ctn child-element">
        <div class="lab-ctn-ctn-title">
          <h1>{{ctn.title}}</h1>
          <p>
            <span>{{ctn.time}}</span> <span>{{ctn.count}}</span>
          </p>
        </div>
        <div class="lab-ctn-ctn-ctn" id="labCtn">
          <!--<p>DT ParkIPFEC(Family Entertainment-->
            <!--Center)</p>-->
          <!--<img src="../../assets/lab/lab01_img01.jpg" alt="">-->
          <!--<p>DT-->
            <!--Park-</p>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import appHeader from '../../components/app-header'
export default {
  name: 'lab-item',
  props: {
  },
  components: {
    appHeader
  },
  data () {
    return {
      ctn: {
        title: '',
        time: '',
        count: 0,
        content: ''
      }
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData () {
      let path = this.$route.path.split('/')
      // 
      this.axios.get(this.GLOABL.URL + 'labs/getLab', {
        params: {
          id: path[path.length - 1]
        }
      }).then((res) => {
        if (res) {
          this.ctn = res.data.data[0]
          let labCtn = document.getElementById('labCtn')
          labCtn.innerHTML = this.ctn.content
        }
      })
      // 
      this.axios.get(this.GLOABL.URL + 'add/addBrowse', {
        params: {
          id: path[path.length - 1],
          type: 'lab'
        }
      }).then((res) => {})
    }
  }
}
</script>

<style lang="less">
  /*@media only screen and (max-width: 750px) {*/
    .lab-ctn {
      position: absolute;
      width: 100%;
      height: auto;
      min-height: 100%;
      color: #bfbfbf;
      &-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url("../../assets/home/twoPage_bg.jpg") repeat;
      }
      &-ctn {
        position: relative;
        width: calc(100% - 1.55rem);
        margin: auto;
        margin-top: 4.5rem;
        &-title {
          height: 2.5rem;
          margin-top: 1.125rem;
          border-bottom: 0.025rem #ccc dashed;
          text-align: center;
          p {
            font-size: 0.5rem;
            height: 1.6rem;
            line-height: 1.6rem;
            color: #606060;
          }
        }
        &-ctn {
          margin-bottom: 0.55rem;
          p {
            font-size: 0.5rem;
            line-height: 0.85rem;
            /*margin-top: 0.85rem;*/
            /*margin-bottom: 0.85rem;*/
            &:first-child{
              margin-top: 1rem;
            }
            img{
              width: 100%;
            }
          }
          img {
            width: 100%;
          }
        }
      }
    }
  /*}*/
</style>



