<template>
  <div class="pcnewDetail-wrap">
    <div class="details-header">
      <h1 class="w3-center">{{title}}</h1>
      <p class="w3-center pcnewDetails-title">
        <span>{{time}}</span>
        <span><i>{{count}}</i></span>
      </p>
    </div>
    <div class="pcnewDetails-text text-ctn-p" id="details">
      <!--<p class="mt40"> Suranga Chandratillake  Balderton Capital  Balderton  Blinkx  CEOBlinkx  2007 </p>-->
      <!--<p class="mt40">-->
        <!-- 8 </p>-->
      <!--<p class="mt40"></p>-->
      <!--<p>-->
        <!-- AppAnnieAlexaLinkedinGitHubProductHunt -->
        <!---->
        <!--</p>-->
      <!--<p class="mt40"></p>-->
      <!--<p>-->
        <!--()()-->
        <!--</p>-->
      <!--<p>()</p>-->
      <!--<p></p>-->
      <!--<p></p>-->
      <!--<p>; Google  Facebook ; CEO-->
      <!--</p>-->
      <!--<p>-->
        <!--</p>-->
      <!--<p>() </p>-->
      <!--<p> </p>-->
      <!--<p></p>-->
    </div>
  </div>
</template>

<script>
export default {
  name: 'newDetails',
  data () {
    return {
      title: '',
      time: '',
      count: 0,
      content: ''
    }
  },
  mounted () {
    this.getDetails()
  },
  methods: {
    getDetails () {
      let path = this.$route.path.split('=')
      // 
      this.axios.get(this.GLOABL.URL + 'news/getNew', {
        params: {
          id: path[path.length - 1]
        }
      }).then((res) => {
        if (res) {
          this.title = res.data.data[0].title
          this.time = res.data.data[0].time
          this.count = res.data.data[0].count
          document.getElementById('details').innerHTML = res.data.data[0].content
        }
      })
      // 
      this.axios.get(this.GLOABL.URL + 'add/addBrowse', {
        params: {
          id: path[path.length - 1],
          type: 'new'
        }
      }).then((res) => {})
    }
  }
}
</script>

<style lang="less">
  .pcnewDetail-wrap {
    width: 75%;
    margin: auto
  }
  .details-header{
    border-bottom: 1px dashed #303030;
  }
  .pcnewDetails-title{
    height: 84px;
    line-height: 84px;
  }
  .pcnewDetails-text{
    margin-top: 30px;
    margin-bottom: 95px;
  }
</style>



