<template>
  <div class="tabbar" ref="wrapper">
      <div class="bscroll-container">
        <!--  -->
        <div class="top-tip">
          <span class="refresh-hook">{{pulldownMsg}}</span>
        </div>
        <!--  -->
        <slot></slot>
        <!--  -->
        <div class="bottom-tip" :style="{display: loadHook}">
          <span class="loading-hook">{{pullupMsg}}</span>
        </div>
      </div>
    <!-- alert -->
    <div class="alert-hook" :style="{display: alertHook}"></div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'better-scroll',
  props: {
    /**
       * probeType
       * 1 scroll
       * 2 scroll
       * 3 scrollswipescroll
       */
    probeType: {
      type: Number,
      default: 1
    },
    /**
       * click
       */
    click: {
      type: Boolean,
      default: true
    },
    /**
       * 
       */
    scrollX: {
      type: Boolean,
      default: false
    },
    /**
       * 
       */
    listenScroll: {
      type: Boolean,
      default: false
    },
    /**
       * 
       */
    data: {},
    /**
     * 
     */
    pulldownMsg: {
      type: String,
      default: ''
    },
    /**
     * 
     */
    pullupMsg: {
      type: String,
      default: ''
    },
    /**
     * 
     */
    alertHook: {
      type: String,
      default: 'none'
    },
    loadHook: {
      type: String,
      default: 'none'
    },
    /**
       * 
       */
    pullup: {
      type: Boolean,
      default: false
    },
    /**
       * 
       */
    pulldown: {
      type: Boolean,
      default: false
    },
    /**
       * 
       */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /**
       * scroll
       */
    refreshDelay: {
      type: Number,
      default: 20
    }
  },
  mounted () {
    // DOMbetter-scroll
    setTimeout(() => {
      this._initScroll()
    }, 20)
  },
  methods: {
    // refreshalert () { // 
    //   this.alertHook = 'block'
    //   setTimeout(() => {
    //     this.alertHook = 'none'
    //   }, 1000)
    // },
    _initScroll () {
      if (!this.$refs.wrapper) {
        return
      }
      // better-scroll
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX
        // scrollbar: true
      })

      // 
      if (this.pullup) {
        this.scroll.on('scrollEnd', () => {
          // 
          if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
            this.$emit('pullup')
          }
        })
      }

      // 
      if (this.pulldown) {
        this.scroll.on('touchEnd', (pos) => {
          // 
          if (pos.y > 50) {
            this.$emit('pulldown')
          }
        })
      }

      // 
      if (this.listenScroll) {
        this.scroll.on('scroll', (pos) => {
          if (pos.y > 30) {
            this.$emit('scroll', pos)
          }
        })
      }
      // 
      if (this.beforeScroll) {
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScroll')
        })
      }
    },
    refresh () {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh()
    }
  },
  watch: {
    // refreshDelayrefresh
    data () {
      setTimeout(() => {
        this.refresh()
      }, this.refreshDelay)
    }
  }
}
</script>

<style scoped>
  .wrapper{
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: url("../assets/home/twoPage_bg.jpg") no-repeat;
    background-size: 100% 100%;
  }
   /* */
  .top-tip{
    position: absolute;
    top: -1rem;
    left: 0;
    width: 100%;
    height: 1rem;
    line-height: 1rem;
    text-align:center;
    color: white;
  }

  .bottom-tip{
    display: none;
    width: 100%;
    height: 0.8rem;
    line-height: 0.8rem;
    text-align: center;
    background: url("../components/loading.gif") no-repeat;
    background-size: auto 100%;
    background-position: center;
    position: absolute;
    bottom: -1rem;
    left: 0;
  }

   /**/
  .alert-hook{
    display: none;
    position: fixed;
    top: 62px;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 1rem;
    line-height: 1rem;
    text-align: center;
    color: #fff;
    font-size: 12px;
    background: rgba(7, 17, 27, 0.5);
  }
</style>



