<template>
  <div v-if="isShow">
    <img src="../assets/serve/serve01_bg.jpg" class="page_bg">
    <div class="w3-display-position serve-ctn pc-wrap">
      <div class="serve-ctn-bg"></div>
      <div class="serve-ctn-ctn">
        <div class="serve-ctn-ctn-title">
          <h1>{{Item.title}}
            <span>{{Item.english}}</span>
          </h1>
          <span class="close" @click="Edit('isShow')"></span>
        </div>
        <div class="serve-ctn-ctn-ctn content">
          <vue-scroll :ops="ops">
            <div class='child-element'>
              <h1 class="serve-ctn-ctn-ctn-title">3D</h1>
              <p>
                3D3D3D3D3D3DT3D
              </p>
              <img src="../assets/serve/01/serve01-img-01.jpg" alt="img">
              <img src="../assets/serve/01/serve01-title-01.jpg" alt="title">
              <p>3DLED3D3D</p>
              <img src="../assets/serve/01/serve01-img-02.jpg" alt="img">
              <img src="../assets/serve/01/serve01-title-02.jpg" alt="title">
              <p>3DLED3D3D</p>
              <img src="../assets/serve/01/serve01-img-03.jpg" alt="img">
              <img src="../assets/serve/01/serve01-title-03.jpg" alt="title">
              <p>4K3D</p>
              <img src="../assets/serve/01/serve01-img-04.jpg" alt="img">
              <img src="../assets/serve/01/serve01-title-04.jpg" alt="title">
              <p>3D mapping</p>
              <img src="../assets/serve/01/serve01-img-05.jpg" alt="img">
              <img src="../assets/serve/01/serve01-title-06.jpg" alt="title">
            </div>
          </vue-scroll>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Close from '../components/close'
export default {
  name: 'serve-item',
  // components: {Close},
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    Item: {
      type: Object,
      default: () => {}
    }
  },
  mounted () {
    this.getServeItemCtn()
  },
  methods: {
    Edit (attr) {
      this.$emit('RemoveInnerPage', attr)
    },
    getServeItemCtn () {
      let ItemCtn = document.getElementById('ItemCtn')
      ItemCtn.innerHTML = this.Item.ctn
    }
  },
  data () {
    return {
      ops: {
        vuescroll: {},
        scrollPanel: {
          scrollingX: false,
          speed: 500,
          easing: undefined,
          padding: true
        },
        rail: {
          background: '#000',
          opacity: 1,
          size: '4px',
          gutterOfEnds: '0px',
          gutterOfSide: '0px'
        },
        bar: {
          background: '#826a23',
          keepShow: true,
          right: '-20px'
        }
      },
      ctn: {}
    }
  }
}
</script>

<style scoped lang="less">
  @width: 100%;
  .serve-ctn {
    position: absolute;
    top: 7.66667%;
    left: 50%;
    transform: translateX(-50%);
    color: #bfbfbf;
    min-height: 82.22222%;
    overflow: hidden;
    &-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url("../assets/home/twoPage_bg.jpg") no-repeat;
      background-size: 100% 100%;
    }
    &-ctn{
      position: relative;
      padding: 0 37px;
      &-title{
        height: 100px;
        margin-top: 17px;
        margin-right: 35px;
        background: url("../assets/home/threePageTitle.png") no-repeat;
        background-size: 100% 100%;
      }
      h1{
        color: #dbdbdb;
        height: 60px;
        line-height: 60px;
        span{
          font-size: 12px;
        }
      }
    }
    &-ctn-ctn {
      height: 605px;
      /*overflow: auto;*/
      margin-bottom: 45px;
      &-title{
        margin-top: 55px;
        margin-bottom: 55px;
      }
      p{
        font-size: 14px;
        margin-bottom: 20px;
        line-height: 24px;
        letter-spacing: 1px;
      }
      img{
        width: 100%;
        /*margin-top: 24px;*/
        &[alt='title']{
          margin-top: 96px;
          margin-bottom: 24px;
        }
      }
      p + img, p + p{
        margin-bottom: 40px;
      }
      img[alt='img'] + img{
        margin-top: 24px;
      }
      .child-element{
        padding-right: 35px;
      }
      h1, img{
        text-align: center;
      }
    }
  }
  /*.division{*/
    /*margin-top: 30px;*/
    /*h1{*/
      /*display: flex;*/
      /*justify-content: space-between;*/
      /*text-align: center;*/
      /*height: 83px;*/
      /*line-height: 83px;*/
    /*}*/
    /*.line{*/
      /*width: 38%;*/
      /*height: 83px;*/
      /*line-height: 83px;*/
      /*display: inline-block;*/
      /*background: url("../assets/serve/serve_ctn_div.png") repeat-x;*/
      /*background-position: center;*/
    /*}*/
  /*}*/
</style>



